px;\r\n  margin: 30px;\r\n  overflow: hidden;\r\n  color: var(--text-color);\r\n  transition: transform 0.3s, box-shadow 0.3s;\r\n  position: relative;\r\n}\r\n\r\n\r\n.restaurant-item:hover {\r\n  transform: translateY(-5px);\r\n  box-shadow: rgba(17, 17, 26, 0.1) 0px 1px 0px, rgba(17, 17, 26, 0.1) 0px 8px 24px, rgba(17, 17, 26, 0.1) 0px 16px 48px;}\r\n\r\n.restaurant-item img {\r\n  max-width: 300px;\r\n  height: 250px;\r\n}\r\n\r\n.restaurant-name {\r\n  min-width: 44px;\r\n  min-height: 44px;\r\n  font-size: 25px;\r\n  font-weight: bold;\r\n  margin-top: 10px;\r\n  padding: 0.5rem 1rem;\r\n  color: var(--text-color);\r\n  text-decoration: none;\r\n}\r\n\r\n.restaurant-description {\r\noverflow: hidden;\r\npadding: 0.5rem 1rem;\r\ntext-overflow: ellipsis;\r\ndisplay: -webkit-box;\r\n-webkit-line-clamp: 4; \r\n-webkit-box-orient: vertical;\r\n}\r\n\r\n.restaurant-rating {\r\n  position: absolute;\r\n  top: 10px;\r\n  right: 10px;\r\n  background-color: rgba(118, 110, 101, 0.7);  \r\n  color: #FFB000;\r\n  padding: 5px 5px;\r\n  border-radius: 5px;\r\n  font-weight: bold;\r\n  display: flex;\r\n  align-items: center;\r\n}\r\n\r\n.restaurant-rating::before {\r\n  content: \"\\2605\";\r\n  margin-right: 5px;\r\n}\r\n\r\n.restaurant-city {\r\n  font-size: 11px;\r\n  font-weight: bold;\r\n  color: var(--body-bg);\r\n  text-transform: uppercase;\r\n  text-align: center;\r\n  line-height: 20px;\r\n  transform: rotate(-45deg);\r\n  -webkit-transform: rotate(-45deg);\r\n  width: 100px;\r\n  display: block;\r\n  background:var(--text-color);\r\n  box-shadow: 0 3px 10px -5px var(--text-color);\r\n  position: absolute;\r\n  top: 19px; left: -21px;\r\n}\r\n\r\n/* Detail Page */\r\n.restaurant {\r\nmargin: 0 auto;\r\nwidth: 100%;\r\nmax-width: 800px;\r\ndisplay: grid;\r\ngrid-template-columns: 1fr;\r\ngap: 24px;\r\nalign-items: center;\r\n}\r\n\r\n.restaurant .hero-image {\r\nposition: relative;\r\nwidth: 100%;\r\nmax-width: 100%;\r\npadding-bottom: 50%;\r\noverflow: hidden;\r\n}\r\n.hero-image .restaurant-name {\r\nposition: absolute;\r\nbottom: 20px; \r\nfont-size: 32px;\r\nfont-weight: bold;\r\ncolor: #fff;\r\ntext-shadow: 2px 2px 4px rgba(0, 0, 0, 0.5); \r\n}\r\n\r\n.hero-image .restaurant-poster {\r\nposition: absolute;\r\nwidth: 100%;\r\nheight: 100%;\r\nobject-fit: cover;\r\nz-index: -1;\r\n}\r\n.hero-image .restaurant-rating {\r\nposition: absolute;\r\ntop: 10px;\r\nright: auto;\r\nleft: 10px;\r\nbackground-color: rgba(118, 110, 101, 0.7);  \r\ncolor: #FFB000;\r\npadding: 5px 5px;\r\nborder-radius: 5px;\r\nfont-weight: bold;\r\ndisplay: flex;\r\nalign-items: center;\r\n}\r\n\r\n.hero-image .restaurant-rating::before {\r\ncontent: \"\\2605\";\r\nmargin-right: 5px;\r\n}\r\n\r\n/* Styling Restaurant Information */\r\n.restaurant__info {\r\nmargin: 20px 0;\r\npadding: 10px;\r\nborder: 2px solid #ccc;\r\nborder-radius: 5px;\r\nbackground-color: #f9f9f9;\r\n}\r\n\r\n.restaurant__info h3 {\r\nfont-size: 24px;\r\ncolor: var(--main-color);\r\nborder-bottom: 2px solid var(--main-color);\r\ntext-transform: uppercase;\r\nmargin: 0 0 16px; \r\n}\r\n\r\n.restaurant__info h4 {\r\nfont-size: 16px;\r\ncolor: #777;\r\nmargin-top: 10px;\r\n}\r\n\r\n.restaurant__info p {\r\nfont-size: 14px;\r\nmargin: 5px 0;\r\n}\r\n\r\n/* Styling Restaurant Description */\r\n.restaurant__description {\r\nmargin: 20px 0;\r\n}\r\n\r\n.restaurant__description h3 {\r\nfont-size: 24px;\r\ncolor: var(--main-color);\r\nborder-bottom: 2px solid #333;\r\npadding-bottom: 10px;\r\nmargin-bottom: 20px;\r\ntext-transform: uppercase;\r\n}\r\n\r\n.restaurant__description p {\r\nfont-size: 20px;\r\ncolor: #777;\r\n}\r\n\r\n/* Styling Restaurant Menu */\r\n.restaurant__menu {\r\n  margin: 20px 0;\r\n}\r\n\r\n.restaurant__menu h3 {\r\n  font-size: 24px;\r\n  color: var(--main-color);\r\n  border-bottom: 2px solid #333;\r\n  padding-bottom: 10px;\r\n  margin-bottom: 20px;\r\n  text-transform: uppercase;\r\n}\r\n\r\n.restaurant__menu ul {\r\n  list-style: none;\r\n  padding: 0;\r\n}\r\n\r\n.restaurant__menu li {\r\n  font-size: 20px;\r\n  color: #555;\r\n  margin: 10px 0;\r\n  position: relative;\r\n  padding-left: 25px;\r\n}\r\n\r\n.restaurant__menu li:before {\r\n  content: \"•\";\r\n  color: var(--main-color);\r\n  position: absolute;\r\n  left: 0;\r\n  margin-left: -20px;\r\n}\r\n\r\n/* Styling Restaurant Reviews */\r\n.restaurant__reviews {\r\n  margin: 20px 0;\r\n}\r\n\r\n.restaurant__reviews h3 {\r\n  font-size: 24px;\r\n  color: var(--main-color);\r\n  border-bottom: 2px solid #333;\r\n  padding-bottom: 10px;\r\n  margin-bottom: 20px;\r\n  text-transform: uppercase;\r\n}\r\n\r\n.restaurant__reviews ul {\r\n  list-style: none;\r\n  padding: 0;\r\n  overflow-y: auto;\r\n  max-height: 300px; \r\n}\r\n\r\n.restaurant__reviews li {\r\n  border-left: 5px solid #f9c325;\r\n  padding: 10px;\r\n  margin: 10px 0;\r\n  background-color: #f9f9f9;\r\n  box-shadow: 0px 2px 4px rgba(0, 0, 0, 0.1);\r\n  position: relative;\r\n  transition: transform 0.2s, box-shadow 0.2s;\r\n}\r\n\r\n.restaurant__reviews li:hover {\r\n  transform: translateY(-5px);\r\n  box-shadow: 0px 6px 12px rgba(0, 0, 0, 0.2);\r\n}\r\n\r\n.restaurant__reviews li:before {\r\n  color: #f9c325;\r\n  position: absolute;\r\n  left: -15px;\r\n}\r\n\r\n.restaurant__reviews li p {\r\n  font-size: 16px;\r\n  color: #555;\r\n  margin: 10px 0;\r\n}\r\n\r\n.restaurant__reviews li::after {\r\n  content: \"\";\r\n  display: block;\r\n  clear: both;\r\n}\r\n\r\n/*\r\nLike\r\n*/\r\n\r\n.like {\r\nwidth: 55px;\r\nheight: 55px;\r\ndisplay: flex;\r\nalign-items: center;\r\njustify-content: center;\r\nbackground-color: var(--main-color);\r\n\r\nposition: fixed;\r\nbottom: 16px;\r\nright: 16px;\r\nborder-radius: 50%;\r\nborder: 0;\r\n\r\nfont-size: 18px;\r\ncolor: white;\r\ncursor: pointer;\r\n}\r\n\r\nfooter {\r\nbackground-color: var(--main-color); \r\ncolor: #fff; \r\ntext-align: center;\r\npadding: 10px 0; \r\n}\r\n\r\n.about-container {\r\nmax-width: 800px;\r\nmargin: 0 auto;\r\npadding: 20px;\r\nbackground-color: #f9f9f9;\r\nborder-radius: 5px;\r\nbox-shadow: 0 0 10px rgba(0, 0, 0, 0.1);\r\n}\r\n\r\n.about-title {\r\nfont-size: 24px;\r\nfont-weight: bold;\r\nmargin-bottom: 20px;\r\n}\r\n\r\n.about-paragraph {\r\nfont-size: 16px;\r\nmargin-bottom: 20px;\r\nline-height: 1.5;\r\ncolor: #333;\r\n}\r\n\r\n.about-paragraph:last-child {\r\nmargin-bottom: 0;\r\n}\r\n\r\n/* Scroll Bar */\r\n*::-webkit-scrollbar {\r\nheight: 8px;\r\nwidth: 8px;\r\n}\r\n*::-webkit-scrollbar-track {\r\nborder-radius: 5px;\r\nbackground-color: #F0F0F0;\r\n}\r\n\r\n*::-webkit-scrollbar-track:hover {\r\nbackground-color: #F0F0F0;\r\n}\r\n\r\n*::-webkit-scrollbar-track:active {\r\nbackground-color: #F0F0F0;\r\n}\r\n\r\n*::-webkit-scrollbar-thumb {\r\nborder-radius: 5px;\r\nbackground-color: #7A3E3E;\r\n}\r\n\r\n*::-webkit-scrollbar-thumb:hover {\r\nbackground-color: #7A3E3E;\r\n}\r\n\r\n*::-webkit-scrollbar-thumb:active {\r\nbackground-color: #7A3E3E;\r\n}\r\n"],"sourceRoot":""}]);
